import './ContentRatings.css';
import { animated, useSpring } from '@react-spring/web';
import MyNavbar from '../MyNavbar';
import { Row, Col, Container, Button } from 'react-bootstrap';
import { useEffect } from 'react';


function ContentRatings() {
  const fadein_props = useSpring({
    from: { opacity: 0, y: -100 },
    to: { opacity: 1, y: 0 },

    reset: false,
    delay: 500
  });

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="ContentRatings">
      <MyNavbar active='content_ratings' />
      <animated.div style={fadein_props}>
        <h1 className='Title'>content ratings</h1>

        <p id='introParagraph'>
          Content ratings are present on my content to indicate the approximate age range at
          which a particular content may be suitable or yet too inappropriate for minors of
          a certain age range.
        </p>

        <Button href="https://docs.google.com/document/d/1aJitFejEVr4OXI5lT3KxR1T18eRpTK1OtUKv-mj_ihE/edit?usp=sharing"
          target="_blank" rel="noopener noreferrer" id='contentRatingGuidelinesButton' variant='info'>
          CONTENT RATING GUIDELINES
        </Button>
        <br />

        <Container className='ratingSummary'>
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/00G.png')} alt='G' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h4><u>Generally suitable for viewing by all ages.</u></h4>
              Content classified G is not necessarily content for kids. There may be some light language, crude content
              and humour, violence and scary content, and distress, but these should be mild enough to be appropriate
              for school-attending kids (usually ages 5 - 6 and up).
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/09+.png')} alt='9+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h4><u>May not be suitable for viewing by children under 9.</u></h4>
              At 9+, more coarse language, violence, frightening imagery may occur. More serious real-life challenges
              and mild suggestive content may begin to appear as well.
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/12+.png')} alt='12+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h4><u>Not recommended for viewing by children under 12.</u></h4>
              Mature themes can begin to appear at 12+. Along with such topics may be occasional strong language,
              sexual themes, more intense violence and horror, and some drug use.
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/15+.png')} alt='15+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h4><u>Not recommended for viewing by minors under 15.</u></h4>
              Mature content, which may include strong language, sexual content, strong violence and gore, and drug use,
              become more prominent at 15+ and thus may not be suitable for younger teenagers.
            </Col>
          </Row><br />
          <Row>
            <Col xs={1} md={2} ><img src={require('./content_ratings/18+.png')} alt='18+' className='ratingLogo' /></Col>
            <Col xs={12} md={10} >
              <h4><u>Suitable for viewing by adults 18 and up only.</u></h4>
              18+ indicates that the content may include extreme or explicit material that can disturb adults of any age.
            </Col>
          </Row><br />
        </Container>
      </animated.div>
    </div>
  );
}

export default ContentRatings;
